import React from 'react';
import '../../../styles/labReports.css'

import { NavigationItem } from '../../navigationItem';


export default function MainSectionLW1({ onDescribeChange }) {
  const getId = (event) => {
    const id = event.target.dataset.infoid;
    onDescribeChange(id);
  }

  const navigationItems = [
    {infoid: "describe", text: '  ', variant: 'primary' },
    {infoid: "goal", text: '      1', variant: 'primary' },
    {infoid: "structure", text: ' ', variant: 'primary' },
    {infoid: "code1", text: 'HTML- ', variant: 'secondary' },
    {infoid: "code2", text: 'HTML- ', variant: 'secondary' },
    {infoid: "code3", text: 'HTML- ', variant: 'secondary' },
    {infoid: "conclusions", text: '', variant: 'primary' }
  ];

  return (
    <section className="mainContent">
      {navigationItems.map((item, idx) => (
        <NavigationItem
          key={idx}
          infoid={item.infoid}
          onclick={getId}
          variant={item.variant}
          text={item.text}
        />
      ))}
    </section>
  )
}
